import React, {useState, useEffect} from 'react';
import {easeInOutQuad} from 'tween-functions';
import LoadingButton from "./Loading-button.js";
import ReactTimeout from 'react-timeout';
import {useEffectOnce} from 'react-use/lib/useWindowSize';
import logo from './logo.jpg';
import Confetti from 'react-confetti';
import './App.css';

function useWindowSize() {
    const isClient = typeof window === 'object';

    function getSize() {
        return {
            width: isClient ? window.innerWidth : undefined,
            height: isClient ? window.innerHeight : undefined
        };
    }

    const [windowSize, setWindowSize] = useState(getSize);

    useEffect(() => {
        if (!isClient) {
            return false;
        }

        function handleResize() {
            setWindowSize(getSize());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []); // Empty array ensures that effect is only run on mount and unmount

    return windowSize;
}

class App extends React.Component {
    constructor(props) {
        super(props);

        this.setLoading = this.setLoading.bind(this);
        this.updateValue = this.updateValue.bind(this);

        this.state = {
            products: [],
            isLoading: false,
            settings: {
                startVelocity: 45,
                elementCount: 30000,
                dragFriction: 0.1,
                duration: 5000,
            }
        };

        this.state = {
            RunConfettiAnimation: false
        }
    }

    componentDidMount() {

        this.getProducts()
    }

    getProducts = () =>{
        fetch("http://localhost:4000/products")
            .then(response => response.json())
            .then(response => this.setState({
                products: response.data
            }))
            .catch(err => {
                console.error(err)
            })
    }

    setLoading(value) {
        this.setState({ isLoading: value });
    }

    updateValue(prop, evt, encode) {
        const value = encode(evt.target.value);
        this.setState(prevState => ({
            settings: Object.assign({}, prevState.settings, { [prop]: value })
        }));
    }

    handleButttonClick = (e) => {
        this.setState({
            RunConfettiAnimation: true
        });
        this.props.setTimeout(this.stopAnimation, 2000)
    };

    stopAnimation = () => {
        this.setState({
            RunConfettiAnimation: false
        })
    };

    render() {
        const {products, settings, isLoading } = this.state;
        return (
            <div>
                {/*{
                    products ? products.map((item, index) => <div key={index}>{item.name}</div>)
                        : <div/>
                }*/}
                <FunctionApp settings={settings}
                             isLoading={isLoading}
                             setLoading={this.setLoading}
                             onClick={this.handleButttonClick}
                             isAnimationOn={this.state.RunConfettiAnimation}
                />
            </div>
        );
    }
}

function FunctionApp(props) {
    const {width, height} = useWindowSize();
    const [windowWidth, setWidth] = useState(width);
    const [list, updateList] = useState([]);
    const [rate, updateRate] = useState(0);
    const [tweens, updateTweens] = useState(easeInOutQuad(1, 0, 0, 2000));
    const Tweens = () => {
        updateTweens(easeInOutQuad(1, 300, 0, 5000));
        const tweenFunction = easeInOutQuad(1, 300, 0, 5000);
        updateRate(tweenFunction);
        const tweenFunction2 = easeInOutQuad(1, 0, 300, 5000);
        updateRate(tweenFunction2);
        //setTimeout(updateRate(0), 5000);
    };

    useEffect(() => {
        const handleResize = () => {
            setWidth(window.innerWidth)
        };
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    });

    function handleClick(props) {
        updateList([...list, "AMELIA SUCKS"]);
        props.onClick()
    }
    return (
        <div className="App">
            <header className="App-header">

                <LoadingButton
                    isLoading={props.isLoading}
                    setLoading={props.setLoading}
                    confettiConfig={props.settings}
                />

                {
                    list.map((item, index) => <div key={index}>{item}</div>)
                }
                <div>
                    AMELIA SUCKS
                </div>
            </header>
        </div>
    )
}

function ConfettiAdjust(props) {
    return (
        <Confetti className="confetti"
                  confettiSource={{x: 300, y: 300, w: 2, h: 2}}
                  friction={0.99}
                  wind={0}
                  gravity={0.08}
                  initialVelocityY={20}
                  initialVelocityX={2}
                  recycle={props.on}
        />
    )
}

function SpinningLogo(props) {
    return (
        <div className="buttonDiv">
            <button onClick={() => props.onClick()} className="profileButton">
                <img src={logo} className="App-logo" alt="logo"/>
            </button>

        </div>

    );
}

export default ReactTimeout(App);
